import { InitialEmscriptenModule, LensCoreModule } from "../../lens-core-module/generated-types";

type MockModule = InitialEmscriptenModule & Partial<LensCoreModule>;

export function loadScript(): Promise<HTMLScriptElement> {
    global.createLensesModule = jest.fn().mockImplementation((module: MockModule) => {
        return new Promise((resolve) => {
            module.getCoreVersion = () => 0;
            module.instantiateWasm!({}, () => resolve(module));
        });
    });
    return Promise.resolve(document.createElement("script"));
}
